/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --text:					#454545;
  --text-light:			rgba(69,69,69,.7);
  --app-color:			#fee298;
  --app-color-focus:		rgba(254,226,152,.5);
  --dividers:				#e6e6e6;
  --titlebar:				#454545;
  --header:				linear-gradient(#edebed, #d6d4d6) no-repeat border-box;
  --header-border:		#b5b4b5;
  --form-control:			#409BFF;
  --edit: 				#ffffff;
  --edit-hover:			rgba(69,69,69,.5);
  --edit-active:			rgba(69,69,69,.8);
  --modal:				rgba(240,239,240,.95);
  --window:				#ededed;
  --input-color:			#000000;
  --input-border:			#cccccc;
  --input-background:		#ffffff;
  --select:				#ffffff;
  --select-border:		#b2b2b2;
  --button:				#ffffff;
  --button-color:			rgba(0,0,0,.85);
  --button-border:		#c0c0c0;
  --button-hover:			linear-gradient(#6cb3fa, #067dff);
  --button-color-hover:	#ffffff;
  --button-border-hover:	linear-gradient(#50a4f9, #005cff);
  --link:					#1e90ff;
  --hover:				#005ee0;
  --code:					#e6e6e6;
  --code-color:			#777777;
  --mark:					#000000;
  --table-border:			#dddddd;
  --table-head:			#ffffff;
  --table-stripe:			rgba(69,69,69,.03);
}

