*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  user-select: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  color: var(--text);
}

::placeholder {
  opacity: .6;
  transition: opacity .2s;
}

:focus::placeholder {
  opacity: 0;
}

body {
  background-color: var(--window);
  padding: 20px;
  color: var(--text);
}

section {
  border-bottom: 1px solid var(--dividers);
  padding-bottom: 10px;
  margin-bottom: 10px;
}

section:last-of-type {
  border-bottom: 0;
}

section div {
  padding-bottom: 10px;
}

section div > label:not(.switch) {
  display: block;
  float: left;
  width: 138px;
  text-align: right;
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
  padding-right: 10px;
}

section div > label:not(.switch)::after {
  content: ':';
}

section div select,
section div input[type="range"] {
  display: block;
  width: calc(100% - 138px);
  font-size: 13.5px;
}

section div input[type=range]::-webkit-slider-thumb {
  margin-top: -5px;
}

section div input[type=range]::-webkit-slider-runnable-track {
  height: 3px;
  border-radius: 2px;
  background: var(--form-control);
}

section div .select-container {
  position: relative;
}

section div .select-container::after {
  content: var(--select-arrow);
  position: absolute;
  top: 0;
  right: 8px;
  pointer-events: none;
}

section div select {
  appearance: none;
  height: 22px;
  padding: 0 6px;
  font-size: 12px;
  background: var(--select);
  border: 1px solid var(--select-border);
  color: var(--input-color);
  border-radius: 3px;
}

section div datalist {
  display: flex;
  justify-content: space-between;
  margin-left: 138px;
}

section div datalist option {
  line-height: 20px;
  font-size: 20px;
}

section div datalist option:first-of-type {
  font-size: 10px;
}

section div .switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
}

section div .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

section div .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 22px;
  transition: .25s;
}

section div .slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  border-radius: 50%;
  transition: .25s;
}

section div input:checked + .slider {
  background-color: var(--form-control);
}

section div input:focus + .slider {
  box-shadow: 0 0 1px var(--form-control);
}

section div input:checked + .slider:before {
  transform: translateX(17px);
}

section div li label {
  font-size: 13px;
}

[data-theme="dark"] section div select {
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
}
